import { useState } from 'react';

const ChatBox = ({ peers, chatMessages, onSendMessage }) => {
  const [message, setMessage] = useState('');

  const handleSend = () => {
    if (message.trim()) {
      onSendMessage(message);
      setMessage('');
    }
  };

  return (
    <div className="chat-box mt-4">
      <h3>Chat</h3>
      <div className="messages" style={{ height: '200px', overflowY: 'scroll', border: '1px solid #ccc', padding: '10px' }}>
        {chatMessages.map((msg, index) => (
          <p key={index}>
            <strong>{msg.peerId}:</strong> {msg.message}
          </p>
        ))}
      </div>
      <input
        type="text"
        value={message}
        onChange={(e) => setMessage(e.target.value)}
        placeholder="Type a message"
        className="form-control mt-2"
      />
      <button onClick={handleSend} className="btn btn-primary mt-2">
        Send
      </button>
    </div>
  );
};

export default ChatBox;