import React from 'react';

const PeerList = ({ peers, onSendFile }) => {
  const getNickname = (id) => `User-${id.slice(0, 4)}`;

  return (
    <div className="card">
      <div className="card-header">
        Connected Peers
      </div>
      <div className="card-body">
        {peers.length === 0 ? (
          <p className="text-muted">No peers connected</p>
        ) : (
          <ul className="list-group">
            {peers.map((peerId) => (
              <li key={peerId} className="list-group-item d-flex justify-content-between align-items-center">
                <span className="text-dark">{getNickname(peerId)}</span>
                <button
                  onClick={() => onSendFile(peerId)}
                  className="btn btn-primary btn-sm"
                >
                  Send File
                </button>
              </li>
            ))}
          </ul>
        )}
      </div>
    </div>
  );
};

export default PeerList;
